
import { defineComponent } from 'vue'
import CurrencyInput from '@/shared/components/CurrencyInput/CurrencyInput.vue'
import { FeeByCustomerInquiryDetail, PFM20400112_REQ_BODY, PFM20400112_RES } from '@/shared/type/PFM20400112.type';
import VueDatePicker from '@vuepic/vue-datepicker';
import '@vuepic/vue-datepicker/dist/main.css'
import { PFM20400121_BODY, PFM20400121_RES } from '@/shared/type/PFM20400121.type';
import { PFM20400131_REQ_BODY, PFM20400131_RES } from '@/shared/type/PFM20400131.type';
import { FeeInfoByMethodTypeCode, PFM20100113_REQ_BODY, PFM20100113_RES } from '@/shared/type/PFM20100113.type';
import { modalController } from '@ionic/vue';
import { FormValidation } from '@/shared/services/FormValidationService';
import ModifyCard from '@/shared/components/ModifyCard/ModifyCard.vue'
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue';
import SelectOption from '@/shared/components/SelectOption/SelectOption.vue'
import { CIB20100114_REQ_BODY, CIB20100114_RES } from '@/shared/type/CIB20100114.type';
import { screenMode } from '@/shared/type/Approval.type';
import { APM20200113_RES } from '@/shared/type/APM20200113.type';

const formValidator = new FormValidation()

export default defineComponent({
    name: 'pfm204001sc01',

    components: {
        VueDatePicker,
        CurrencyInput,
        ModifyCard,
        BreadCrumb,
        SelectOption
    },

    data() {
        return {
            isDisableField: true,
            isButtonDelete: true,
            isDisableDate: true,
            isButtonList: true,
            isButtonCancel: true,
            isAllowEditNameField: false,
            isAllowEditNoField: false,
            isInvalidInput: false,
            isInvalidMinRate: false,
            isInvalidMaxRate: false,
            isInvalidMinFeeAmount: false,
            isInvalidMaxFeeAmount: false,
            isInvalidStartEndDate: false,
            isInvalidCustomerNo: false,
            isInvalidMinMaxBaseAmount: false,
            isInvalidMinMaxBaseRate: false,
            isInvalidBaseFeeRate: false as boolean,
            isDisableFeeCalcBaseCode: false as boolean,
            isInvalidFeeCalcBaseCode: false as boolean,
            isDisableBaseFeeRate: false as boolean,
            isDisableBaseFeeAmount: false as boolean,
            time: {
                hours: new Date().getHours(),
                minutes: new Date().getMinutes()
            },

            customerData: {
                customerNo: '',
                customerName: '',
                productFeeCode: '',
                feeDescription: '',
                feeCurrencyCode: '',
                applySeqNo: '',
                baseFeeApplyStartAmount: '0',
                feeMinAmount: '0',
                applyStartDate: new Date(),
                applyEndDate: new Date(),
                feeCalcApplyBaseCode: '01',
                applyTransactionFeeAmount: '0',
                feeApplyStatus: '',
                transactionFeeTypeCode: '',
                feeReasonCode: '',
                applyFeeRate: '0',
                feeMaxAmount: '0',
                minFeeRate: '0',
                maxFeeRate: '0',
                firstRegisterBranchCode: '',
                firstRegisterTellerID: '',
                firstRegisterTellerName: '',
                firstRegisterDate: '',
                firstRegisterTime: '',
                lastChangeBranchCode: '',
                lastChangeTellerID: '',
                lastChangeTellerName: '',
                lastChangeDate: '',
                lastChangeTime: '',
                deleteBranchCode: '',
                deleteTellerID: '',
                deleteTellerName: '',
                deleteDate: '',
                deleteTime: ''
            },
            tempProductFeeCode: {} as FeeInfoByMethodTypeCode,
            productFeeCodes: [] as FeeInfoByMethodTypeCode[],
            feeCurrencyCode: '',
            productFeeCodeEdit: '',
            modifyCard: {
                registerTellerId: '',
                registerTellerName: '',
                registerDate: '',
                registerTime: '',
                updatedTellerId: '',
                updatedTellerName: '',
                updatedDate: '',
                updatedTime: '',
                deleteTellerId: '',
                deleteTellerName: '',
                deleteDate: '',
                deleteTime: '',
            },
            approvalRes: {} as any,
            operationMode: 'normal' as screenMode,
            productFee: {
                calcCode: '',
                startDate: new Date(),
                endDate: new Date()
            },
        }
    },

    watch: {
        tempProductFeeCode: {
            handler(newFeeInfo: FeeInfoByMethodTypeCode) {
                this.feeCurrencyCode = newFeeInfo.feeCurrencyCode
                this.productFee.startDate = this.$datetime.convertStringToDate(newFeeInfo.applyStartDate)
                this.productFee.endDate = this.$datetime.convertStringToDate(newFeeInfo.applyEndDate)
                this.productFee.calcCode = newFeeInfo.feeCalcApplyBaseCode
                if (this.displayType == 'register') {
                    this.customerData.applyStartDate = this.$datetime.convertStringToDate(newFeeInfo.applyStartDate)
                    this.customerData.applyEndDate = this.$datetime.convertStringToDate(newFeeInfo.applyEndDate)
                }
                if (this.$route.query.type !== "edit") {
                    this.productFeeCodeEdit = `${newFeeInfo.productFeeCode} : ${newFeeInfo.feeName}` || ''
                }
            },
            deep: true
        },
        customerData: {
            handler(newCustomerData) {
                // if (Number(newCustomerData.feeMinAmount) > Number(newCustomerData.feeMaxAmount)) {
                //     this.isInvalidMinFeeAmount = true
                // } else {
                //     this.isInvalidMinFeeAmount = false
                // }
                // if (Number(newCustomerData.minFeeRate) > Number(newCustomerData.maxFeeRate)) {
                //     this.isInvalidMinRate = true
                // } else {
                //     this.isInvalidMinRate = false
                // }


                // ===== Fee Calc = Rate =====
                if (newCustomerData.feeCalcApplyBaseCode == '02') {
                    if (Number(newCustomerData.feeMinAmount) > Number(newCustomerData.feeMaxAmount)) {
                        this.isInvalidMinFeeAmount = true;

                    } else {
                        this.isInvalidMinFeeAmount = false;
                    }
                    this.isDisableFeeCalcBaseCode = false;
                    this.isDisableBaseFeeRate = false;
                    this.isDisableBaseFeeAmount = true;
                    this.customerData.applyTransactionFeeAmount = '0';
                } else {
                    // ===== Fee Calc = Amount =====
                    this.isDisableFeeCalcBaseCode = true;
                    this.isDisableBaseFeeRate = true;
                    this.isDisableBaseFeeAmount = false;
                    this.customerData.applyFeeRate = '0';
                    newCustomerData.feeMaxAmount = newCustomerData.applyTransactionFeeAmount;
                    newCustomerData.feeMinAmount = newCustomerData.applyTransactionFeeAmount;
                }
            },
            deep: true,
        },
        'customerData.customerNo': function (customerNo: string) {
            // this.customerData.customerNo = customerNo.replace(' ', '')
            if (customerNo.trim().length == 7 || customerNo.trim().length == 8) {
                this.onClickSearchCustomerNo()
            } else {
                this.customerData.customerName = ''
            }
        }
    },

    computed: {
        approvalType(): string {
            return this.$route.query.approvalType ? this.$route.query.approvalType.toString() : ''
        },

        displayType(): string {
            if (this.$route.query.type) {
                return this.$route.query.type.toString()
            } else {
                return ''
            }
        },
        formattedStartDate(): string {
            return this.$datetime.formatVueDatePicker(this.customerData.applyStartDate)
        },
        formattedEndDate(): string {
            return this.$datetime.formatVueDatePicker(this.customerData.applyEndDate)
        },
        specialFeeReasonCode(): any {
            return this.$store.state.CommonCodeModule.commonCode.SpecialFeeReasonCode
        },
        specialFeeType(): any {
            return this.$store.state.CommonCodeModule.commonCode.SpecialFeeType
        },
        feeApplyStatusCode(): any {
            return this.$store.state.CommonCodeModule.commonCode.FeeApplyStatusCode
        },
        isInvalidFieldFeeCalcBaseCode(): boolean {
            return this.customerData.feeCalcApplyBaseCode === '' ? true : false;
        },
        invalidFeeMinAmountByFeeCalc(): boolean {
            return Number(this.customerData.feeMinAmount) > Number(this.customerData.feeMaxAmount) ? true : false;
        }
    },

    async created() {
        if (this.approvalType === '') {
            await this.getFeeInfoByMethodTypeCode()

            if (this.$route.query.type === "edit") {

                await this.getFeeByCustomerDetail();
                this.isAllowEditNameField = true
                this.isAllowEditNoField = true
            } else {

                this.isDisableField = false;
                this.isButtonDelete = false;
                this.isButtonList = false;
                this.isAllowEditNameField = false
                this.isAllowEditNoField = false
                this.customerData.feeApplyStatus = this.feeApplyStatusCode ? this.feeApplyStatusCode[0].key : ''
                this.customerData.transactionFeeTypeCode = this.specialFeeType ? this.specialFeeType[0].key : ''
                this.customerData.feeReasonCode = this.specialFeeReasonCode ? this.specialFeeReasonCode[0].key : ''
            }
        } else {
            await this.getFeeInfoByMethodTypeCode()

            await this.$approval.getAuthorizationDetail().then(async (authDetail: APM20200113_RES) => {
                let authDetailParsed: FeeByCustomerInquiryDetail = JSON.parse(authDetail.body.requestContent)
                this.approvalRes = authDetail.body

                this.tempProductFeeCode.productFeeCode = authDetailParsed.productFeeCode
                this.tempProductFeeCode.feeCurrencyCode = authDetailParsed.feeCurrencyCode

                if (this.productFeeCodes.length > 0) {
                    const tempFee = this.productFeeCodes.find((v: FeeInfoByMethodTypeCode) => v.productFeeCode == authDetailParsed.productFeeCode && v.feeCurrencyCode == authDetailParsed.feeCurrencyCode) as FeeInfoByMethodTypeCode
                    if (tempFee) {
                        this.productFeeCodeEdit = `${tempFee.productFeeCode} : ${tempFee.feeName}` || ''
                    }
                }
                this.operationMode = this.approvalType == 'my-request' ? 'read' : this.approvalType == 'my-approval' ? 'approval' : 'normal'

                if (this.$util.checkServiceIdType(authDetail.body.serviceID) == 'delete') {
                    const body: PFM20400112_REQ_BODY = {
                        productFeeCode: `${authDetailParsed.productFeeCode}`,
                        feeCurrencyCode: `${authDetailParsed.feeCurrencyCode}`,
                        applyStartDate: `${authDetailParsed.applyStartDate}`,
                        applySeqNo: Number(`${authDetailParsed.applySeqNo}`),
                        customerNo: `${authDetailParsed.customerNo}`,
                    }

                    // ---- Adapter: PFM20400112_Adapter_FeeByCustomer_InquiryDetail
                    const resData = await this.$request.getRequest('PFM20400112', body, false) as PFM20400112_RES;
                    this.customerData = {
                        customerNo: resData.body.customerNo,
                        customerName: resData.body.customerName,
                        productFeeCode: resData.body.productFeeCode,
                        feeDescription: resData.body.feeDescription,
                        feeCurrencyCode: resData.body.feeCurrencyCode,
                        applySeqNo: `${resData.body.applySeqNo}`,
                        baseFeeApplyStartAmount: `${resData.body.baseFeeApplyStartAmount}`,
                        feeMinAmount: `${resData.body.feeMinAmount}`,
                        applyStartDate: this.$datetime.convertStringToDate(resData.body.applyStartDate),
                        applyEndDate: this.$datetime.convertStringToDate(resData.body.applyEndDate),
                        applyTransactionFeeAmount: `${resData.body.applyTransactionFeeAmount}`,
                        feeCalcApplyBaseCode: resData.body.feeCalcApplyBaseCode,
                        feeApplyStatus: resData.body.feeApplyStatusCode,
                        transactionFeeTypeCode: resData.body.transactionFeeTypeCode,
                        feeReasonCode: resData.body.feeFavorReasonCode,
                        applyFeeRate: resData.body.applyFeeRate.toString(),
                        feeMaxAmount: resData.body.feeMaxAmount.toString(),
                        minFeeRate: resData.body.minFeeRate.toString(),
                        maxFeeRate: resData.body.maxFeeRate.toString(),
                        firstRegisterBranchCode: '',
                        firstRegisterTellerID: '',
                        firstRegisterTellerName: '',
                        firstRegisterDate: '',
                        firstRegisterTime: '',
                        lastChangeBranchCode: '',
                        lastChangeTellerID: '',
                        lastChangeTellerName: '',
                        lastChangeDate: '',
                        lastChangeTime: '',
                        deleteBranchCode: '',
                        deleteTellerID: '',
                        deleteTellerName: '',
                        deleteDate: '',
                        deleteTime: ''
                    }
                } else {
                    this.customerData = {
                        customerNo: authDetailParsed.customerNo,
                        customerName: authDetailParsed.customerName,
                        productFeeCode: authDetailParsed.productFeeCode,
                        feeDescription: authDetailParsed.feeDescription,
                        feeCurrencyCode: authDetailParsed.feeCurrencyCode,
                        applySeqNo: `${authDetailParsed.applySeqNo}`,
                        baseFeeApplyStartAmount: `${authDetailParsed.baseFeeApplyStartAmount}`,
                        feeMinAmount: `${authDetailParsed.feeMinAmount}`,
                        applyStartDate: this.$datetime.convertStringToDate(authDetailParsed.applyStartDate),
                        applyEndDate: this.$datetime.convertStringToDate(authDetailParsed.applyEndDate),
                        applyTransactionFeeAmount: `${authDetailParsed.applyTransactionFeeAmount}`,
                        feeApplyStatus: authDetailParsed.feeApplyStatusCode,
                        transactionFeeTypeCode: authDetailParsed.transactionFeeTypeCode,
                        feeReasonCode: authDetailParsed.feeFavorReasonCode,
                        applyFeeRate: authDetailParsed.applyFeeRate.toString(),
                        feeMaxAmount: authDetailParsed.feeMaxAmount.toString(),
                        minFeeRate: authDetailParsed.minFeeRate.toString(),
                        maxFeeRate: authDetailParsed.maxFeeRate.toString(),
                        feeCalcApplyBaseCode: authDetailParsed.feeCalcApplyBaseCode,
                        firstRegisterBranchCode: '',
                        firstRegisterTellerID: '',
                        firstRegisterTellerName: '',
                        firstRegisterDate: '',
                        firstRegisterTime: '',
                        lastChangeBranchCode: '',
                        lastChangeTellerID: '',
                        lastChangeTellerName: '',
                        lastChangeDate: '',
                        lastChangeTime: '',
                        deleteBranchCode: '',
                        deleteTellerID: '',
                        deleteTellerName: '',
                        deleteDate: '',
                        deleteTime: ''
                    }
                }
            })
        }
    },

    methods: {

        onClickDelete() {
            this.$dialog.onOpen({
                content: this.$t("message.are_you_sure_you_want_to_delete"),
                dialogMode: 'Question',
                callback: () => {
                    modalController.dismiss()
                    this.deleteFeeByCustomer();
                }
            });
        },

        onClickList() {
            this.$router.push('/PFM204001SC00')
        },

        onClickCancel() {
            switch (this.displayType) {
                case 'register': {
                    this.$dialog.onOpen({
                        content: this.$t('message.are_you_sure_you_want_to_cancel_registration?'),
                        dialogMode: 'Question',
                        callback: () => {
                            modalController.dismiss()
                            this.$router.push('/PFM204001SC00')
                        }
                    });
                }
                    break;
                case 'edit': {
                    this.$dialog.onOpen({
                        content: this.$t("message.cancel_without_saving_changes"),
                        dialogMode: 'Question',
                        callback: () => {
                            this.getFeeByCustomerDetail()
                            this.isInvalidInput = this.isInvalidMinFeeAmount = this.isInvalidMaxFeeAmount = this.isInvalidMinRate = this.isInvalidMaxRate = this.isInvalidStartEndDate = false
                            this.isButtonList = true
                            this.isButtonDelete = true
                            this.isDisableField = true
                            this.isButtonCancel = false
                            modalController.dismiss()
                        }
                    });
                }
                    break;
            }
        },

        onClickEdit() {
            this.isButtonDelete = false
            this.isDisableField = false
            this.isButtonList = false
            this.isButtonCancel = true
        },

        onClickSave() {
            this.isInvalidInput = this.isInvalidMinFeeAmount = this.isInvalidMaxFeeAmount = this.isInvalidMinRate = this.isInvalidMaxRate = this.isInvalidStartEndDate = false
            if (this.customerData.customerNo && this.customerData.customerName && this.tempProductFeeCode && this.customerData.feeDescription &&
                this.customerData.baseFeeApplyStartAmount && this.customerData.feeMinAmount &&
                this.customerData.applyEndDate && this.customerData.applyTransactionFeeAmount && this.customerData.feeApplyStatus &&
                this.customerData.transactionFeeTypeCode && this.customerData.feeReasonCode && this.customerData.applyFeeRate && this.customerData.feeMaxAmount &&
                this.customerData.minFeeRate && this.customerData.maxFeeRate) {

                if (!formValidator.startDateEndDateValidation(this.customerData.applyStartDate, this.customerData.applyEndDate)) {
                    this.isInvalidStartEndDate = true
                    this.$dialog.onOpen({
                        content: this.$t("message.would_you_like_to_save"),
                        dialogMode: 'Question',
                        callback: () => {
                            this.submitFeeByCustomerUpdate().then((res: boolean) => {
                                if (res) {
                                    modalController.dismiss()
                                    this.isDisableField = true
                                    this.isButtonList = true
                                    this.isButtonCancel = false
                                    this.isButtonDelete = true
                                }
                            });
                        }
                    });
                } else if (!formValidator.minMaxValueValidation(Number(this.customerData.feeMinAmount), Number(this.customerData.feeMaxAmount))) {
                    this.isInvalidMinFeeAmount = true
                } else {
                    if (this.displayType === 'edit') {
                        this.$dialog.onOpen({
                            content: this.$t("message.would_you_like_to_save"),
                            dialogMode: 'Question',
                            callback: () => {
                                this.submitFeeByCustomerUpdate().then((res: boolean) => {
                                    if (res) {
                                        modalController.dismiss()
                                        this.isDisableField = true
                                        this.isButtonList = true
                                        this.isButtonCancel = false
                                        this.isButtonDelete = true
                                    }
                                });
                            }
                        });

                    } else if (this.displayType === 'register') {

                        this.$dialog.onOpen({
                            content: this.$t("message.would_you_like_to_save"),
                            dialogMode: 'Question',
                            callback: () => {
                                this.submitFeeByCustomerRegister().then((res: boolean) => {
                                    if (res) {
                                        modalController.dismiss()
                                        this.$router.push('/PFM204001SC00')
                                    }
                                });
                            }
                        });
                    }
                }

            } else {
                this.isInvalidInput = true
                this.customerData.minFeeRate == '' ? this.isInvalidMinRate = true : false
                this.customerData.maxFeeRate == '' ? this.isInvalidMaxRate = true : false
                this.customerData.feeMinAmount == '' ? this.isInvalidMinFeeAmount = true : false
                this.customerData.feeMaxAmount == '' ? this.isInvalidMaxFeeAmount = true : false
                // dialog.onOpen({
                //   content: this.$t("message.invalid_input"),
                //   dialogMode: 'Warning',
                //   callback: () => modalController.dismiss()
                // });
            }
        },

        async deleteFeeByCustomer() {
            const reqBody = {
                productFeeCode: this.customerData.productFeeCode,
                feeCurrencyCode: this.customerData.feeCurrencyCode,
                applyStartDate: `${this.$datetime.convertDateToString(this.customerData.applyStartDate)}`,
                customerNo: this.customerData.customerNo,
                applySeqNo: Number(this.customerData.applySeqNo)
            }

            const resData: any = await this.$request.getRequest('PFM20400141', reqBody, false)

            if (resData) {
                this.$dialog.onOpen({
                    content: this.$t("message.delete_success"),
                    dialogMode: 'Success',
                    callback: () => {
                        modalController.dismiss()
                        this.$router.push('/PFM204001SC00')
                    }
                });
                this.$router.push('/PFM204001SC00')
            }
        },

        // Customer Update (PFM20400131)
        async submitFeeByCustomerUpdate(): Promise<boolean> {
            const reqBody: PFM20400131_REQ_BODY = {
                customerNo: this.customerData.customerNo,
                customerName: this.customerData.customerName,
                productFeeCode: this.tempProductFeeCode.productFeeCode,
                feeDescription: this.customerData.feeDescription,
                feeCurrencyCode: this.tempProductFeeCode.feeCurrencyCode,
                applySeqNo: Number(this.customerData.applySeqNo),
                baseFeeApplyStartAmount: Number(this.customerData.baseFeeApplyStartAmount),
                feeMinAmount: Number(this.customerData.feeMinAmount),
                applyStartDate: `${this.$datetime.convertDateToString(this.customerData.applyStartDate)}`,
                applyEndDate: `${this.$datetime.convertDateToString(this.customerData.applyEndDate)}`,
                applyTransactionFeeAmount: Number(this.customerData.applyTransactionFeeAmount),
                feeApplyStatusCode: this.customerData.feeApplyStatus,
                transactionFeeTypeCode: this.customerData.transactionFeeTypeCode,
                feeFavorReasonCode: this.customerData.feeReasonCode,
                applyFeeRate: Number(this.customerData.applyFeeRate),
                feeMaxAmount: Number(this.customerData.feeMaxAmount),
                minFeeRate: Number(this.customerData.minFeeRate),
                maxFeeRate: Number(this.customerData.maxFeeRate),
                feeCalcApplyBaseCode: this.customerData.feeCalcApplyBaseCode,
            }

            const resData = await this.$request.getRequest('PFM20400131', reqBody, false) as PFM20400131_RES

            if (resData.header.result) {
                this.$dialog.onOpen({
                    content: this.$t("message.update_success"),
                    dialogMode: 'Success',
                    callback: () => { modalController.dismiss() }
                });
            }

            return resData.header.result
        },

        async submitFeeByCustomerRegister(): Promise<boolean> {

            const reqbody: PFM20400121_BODY = {
                customerNo: this.customerData.customerNo,
                customerName: this.customerData.customerName,
                productFeeCode: this.tempProductFeeCode.productFeeCode,
                feeDescription: this.customerData.feeDescription,
                feeCurrencyCode: this.tempProductFeeCode.feeCurrencyCode,
                baseFeeApplyStartAmount: Number(this.customerData.baseFeeApplyStartAmount),
                feeMinAmount: Number(this.customerData.feeMinAmount),
                applyStartDate: `${this.$datetime.convertDateToString(this.customerData.applyStartDate)}`,
                applyEndDate: `${this.$datetime.convertDateToString(this.customerData.applyEndDate)}`,
                applyTransactionFeeAmount: Number(this.customerData.applyTransactionFeeAmount),
                transactionFeeTypeCode: this.customerData.transactionFeeTypeCode,
                feeFavorReasonCode: this.customerData.feeReasonCode,
                applyFeeRate: Number(this.customerData.applyFeeRate),
                feeMaxAmount: Number(this.customerData.feeMaxAmount),
                minFeeRate: Number(this.customerData.minFeeRate),
                maxFeeRate: Number(this.customerData.maxFeeRate),
                feeCalcApplyBaseCode: this.customerData.feeCalcApplyBaseCode,
            }

            //---- Adapter: PFM20400121_Adapter_FeeByCustomer_Register
            const resData = await this.$request.getRequest('PFM20400121', reqbody, false) as PFM20400121_RES

            if (resData.header.result) {
                this.$dialog.onOpen({
                    content: this.$t("message.register_success"),
                    dialogMode: 'Success',
                    callback: () => {
                        // this.getFeeByCustomerDetail()
                        modalController.dismiss()
                    }
                });
            }

            return resData.header.result
        },

        async getFeeByCustomerDetail() {
            this.isButtonCancel = false;

            const body: PFM20400112_REQ_BODY = {
                productFeeCode: `${this.$route.query.productFeeCode}`,
                feeCurrencyCode: `${this.$route.query.feeCurrencyCode}`,
                applyStartDate: `${this.$route.query.applyStartDate}`,
                applySeqNo: Number(`${this.$route.query.applySeqNo}`),
                customerNo: `${this.$route.query.customerNo}`,
            }

            // ---- Adapter: PFM20400112_Adapter_FeeByCustomer_InquiryDetail
            const resData = await this.$request.getRequest('PFM20400112', body, false) as PFM20400112_RES;

            this.tempProductFeeCode.productFeeCode = resData.body.productFeeCode
            this.tempProductFeeCode.feeCurrencyCode = resData.body.feeCurrencyCode

            if (this.productFeeCodes.length > 0) {
                const tempFee = this.productFeeCodes.find((v: FeeInfoByMethodTypeCode) => v.productFeeCode == resData.body.productFeeCode && v.feeCurrencyCode == resData.body.feeCurrencyCode) as FeeInfoByMethodTypeCode
                if (tempFee) {
                    this.productFeeCodeEdit = `${tempFee.productFeeCode} : ${tempFee.feeName}` || ''
                }
            }

            this.customerData = {
                customerNo: resData.body.customerNo,
                customerName: resData.body.customerName,
                productFeeCode: resData.body.productFeeCode,
                feeDescription: resData.body.feeDescription,
                feeCurrencyCode: resData.body.feeCurrencyCode,
                applySeqNo: `${resData.body.applySeqNo}`,
                baseFeeApplyStartAmount: `${resData.body.baseFeeApplyStartAmount}`,
                feeMinAmount: `${resData.body.feeMinAmount}`,
                applyStartDate: this.$datetime.convertStringToDate(resData.body.applyStartDate),
                applyEndDate: this.$datetime.convertStringToDate(resData.body.applyEndDate),
                applyTransactionFeeAmount: `${resData.body.applyTransactionFeeAmount}`,
                feeApplyStatus: resData.body.feeApplyStatusCode,
                transactionFeeTypeCode: resData.body.transactionFeeTypeCode,
                feeReasonCode: resData.body.feeFavorReasonCode,
                applyFeeRate: resData.body.applyFeeRate.toString(),
                feeMaxAmount: resData.body.feeMaxAmount.toString(),
                minFeeRate: resData.body.minFeeRate.toString(),
                maxFeeRate: resData.body.maxFeeRate.toString(),
                firstRegisterBranchCode: resData.body.firstRegisterBranchCode,
                firstRegisterTellerID: resData.body.firstRegisterTellerID,
                firstRegisterTellerName: resData.body.firstRegisterTellerName,
                firstRegisterDate: this.$datetime.formatStringToStringDate(resData.body.firstRegisterDate),
                firstRegisterTime: this.$datetime.formatStringToTime(resData.body.firstRegisterTime),
                lastChangeBranchCode: resData.body.lastChangeBranchCode,
                lastChangeTellerID: resData.body.lastChangeTellerID,
                lastChangeTellerName: resData.body.lastChangeTellerName,
                lastChangeDate: this.$datetime.formatStringToStringDate(resData.body.lastChangeDate),
                lastChangeTime: this.$datetime.formatStringToTime(resData.body.lastChangeTime),
                deleteBranchCode: resData.body.deleteBranchCode ? resData.body.deleteBranchCode : '',
                deleteTellerID: resData.body.deleteTellerID ? resData.body.deleteTellerID : '',
                deleteTellerName: resData.body.deleteTellerName ? resData.body.deleteTellerName : '',
                deleteDate: resData.body.deleteDate ? this.$datetime.formatStringToStringDate(resData.body.deleteDate) : '',
                deleteTime: resData.body.deleteTime ? this.$datetime.formatStringToTime(resData.body.deleteTime) : '',
                feeCalcApplyBaseCode: resData.body.feeCalcApplyBaseCode,
            }

            this.modifyCard = {
                registerTellerId: resData.body.firstRegisterTellerID,
                registerTellerName: resData.body.firstRegisterTellerName,
                registerDate: resData.body.firstRegisterDate,
                registerTime: resData.body.firstRegisterTime,
                updatedTellerId: this.customerData.feeApplyStatus === '04' ? '' : resData.body.lastChangeTellerID,
                updatedTellerName: this.customerData.feeApplyStatus === '04' ? '' : resData.body.lastChangeTellerName,
                updatedDate: this.customerData.feeApplyStatus === '04' ? '' : resData.body.lastChangeDate,
                updatedTime: this.customerData.feeApplyStatus === '04' ? '' : resData.body.lastChangeTime,
                deleteTellerId: this.customerData.feeApplyStatus === '04' ? resData.body.lastChangeTellerID : '',
                deleteTellerName: this.customerData.feeApplyStatus === '04' ? resData.body.lastChangeTellerName : '',
                deleteDate: this.customerData.feeApplyStatus === '04' ? resData.body.lastChangeDate : '',
                deleteTime: this.customerData.feeApplyStatus === '04' ? resData.body.lastChangeTime : '',
            }
        },

        async getFeeInfoByMethodTypeCode() {
            const body: PFM20100113_REQ_BODY = {
                feeCalcMethodTypeCode: '',
            }

            // ---- Adapter: PFM20100113_Adapter_FeeInfoByMethodTypeCode_InquiryList
            const resData = await this.$request.getRequest('PFM20100113', body, false) as PFM20100113_RES;

            this.productFeeCodes = resData.body.list

            if (this.$route.query.type !== 'edit') {
                this.tempProductFeeCode = this.productFeeCodes[0]
            }
        },

        async onClickSearchCustomerNo() {
            this.isInvalidCustomerNo = false
            const reqBody: CIB20100114_REQ_BODY = {
                customerNo: this.customerData.customerNo
            }

            const res = await this.$request.getRequest("CIB20100114", reqBody, false, undefined, false, false) as CIB20100114_RES

            if (res.header.result) {
                this.customerData.customerName = res.body.customerFullName
            } else {
                this.isInvalidCustomerNo = true;
            }
        },
    }
})
